.stars {
  margin: 0;
  margin-bottom: 24px;
  
  &__list {
    margin: 0;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
    
  }
    
  &__grup {
    display: flex;
  }
    
  &__star {
    display: flex;
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url(/images/star-active.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
    
  &__star:last-child {
    background-image: url(/images/star.svg);
    background-repeat: no-repeat;
  }
    
  &__text {
    margin: 0;
    
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 16px;
  }
}
