.buy {
  display: flex;
  text-decoration: none;
  width: 164px;
  height: 38px;
  background-color: rgba(0, 172, 220, 1);
  color: rgba(255, 255, 255, 1);
  border: 1px solid #00acdc;
  border-radius: 5px;
  align-items: center;
  text-align: center;
  justify-content: center;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  
  &:hover {
    background-color: #fff;
    color: $main-color;
  }
}
