.main {
  display: flex;
  justify-content: center;
  align-items: center;

  padding-top: 50px;
}

.div {
  display: flex;
  flex-wrap: wrap;

  justify-content: center;
  width: 1024px;

}
  
.card {
  margin-right: 50px;
  margin-bottom: 48px;
  padding: 32px 15px 15px;

  width: 198px;
  height: 406px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  box-sizing: border-box;
  
  &:nth-child(4n) {
    margin-right: 0;
  }
  
  &:nth-last-child(-n + 4) {
    margin-bottom: 0;
  }
  
  &__name {
    font-family: Roboto, sans-serif;
    font-style: normal;
    width: 166px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $header-color;
        
    margin: 0;
    margin-bottom: 4px;
  }
    
  &__code {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-size: 10px;
    line-height: 14px;
    color: $text-color;
    
    margin: 0;
    margin-bottom: 16px;
  }
  
  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto;
    margin-bottom: 40px;
  }
  
  &__logo {
    width: 160px;
    height: 134px;
  }
  
  &__price {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    margin: 0;
  }
    
  &__money {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: #616070;
    margin: 0;
    margin-bottom: 16px;
  }
    
  &__value {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $header-color;
    margin: 0;
    margin-bottom: 16px;
  }
}
