.nav {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
  
  &__link {
    display: inline-block;
    
    font-size: 12px;
    line-height: 60px;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;
    color: $header-color;
  }
  
  &__list {
    display: flex;
    
    padding: 0;
    margin: 0;
    list-style-type: none;
  }
  
  &__item {
    position: relative;
    margin: 0 10px;
  }
  
  &__list :last-child {
    margin-right: 0;
  }
  
  &__list :first-child {
    margin-left: 0;
  }
  
  &__link:hover,
  .is-active {
    color: $main-color;
  }
  
}

.is-active::after {
  box-sizing: border-box;
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  
  width: 100%;
  height: 4px;
  background-color: $main-color;
  border-radius: 8px;
}
